/* Navbar.css */

/* Sidebar styling */
nav {
    display: flex;
    flex-direction: column; /* Stack links vertically */
    align-items: flex-start; /* Align items to the left */
    background-color: #d41717; /* Darker background for contrast */
    padding: 2rem 1rem;
    position: fixed; /* Fixed sidebar */
    top: 0;
    left: 0; /* Align sidebar to the left */
    height: 100%; /* Full height of the viewport */
    width: 120px; /* Set a fixed width for the sidebar */
    box-shadow: 4px 0 10px rgba(0, 0, 0, 0.15); /* Subtle shadow for depth */
    z-index: 2; /* Keep sidebar above content */
    margin-bottom: 1rem; 
}

.navbar-brand {
    margin-bottom: 2rem; /* Add some space between the brand and the links */
    text-align: center; /* Center brand title */
    color: white;
}

.navbar-brand h1 {
    font-size: 1.8rem; /* Larger font size for the brand */
}

.navbar-links {
    display: flex;
    flex-direction: column; /* Stack links vertically */
    align-items: flex-start; /* Align items to the left */
    width: 100%; /* Full width */
}

.navbar-links a {
    color: #ecf0f1; /* Lighter text color */
    text-decoration: none;
    font-weight: 600;
    font-size: 1.2rem;
    padding: 1rem;
    width: 100%; /* Make the link take full width */
    border-radius: 5px; /* Rounded corners */
    transition: background-color 0.3s ease, color 0.3s ease; /* Smooth transition */
    margin-bottom: 1rem; /* Adds space between links */
}

/* Hover effect */
.navbar-links a:hover {
    color: #fff; /* Text color change on hover */
    background-color: #a31b1b; /* Background color change */
    transform: translateY(-3px); /* Slight lift effect */
    font-size: 1.3rem; /* Increase font size on hover */
}

/* Active link (optional) */
.navbar-links a.active {
    color: #fff;
    background-color: #2980b9; /* Darker blue for active state */
    transform: translateY(0); /* No lift effect for active link */
}

/* Mobile responsiveness */
@media (max-width: 768px) {
    nav {
        flex-direction: column; /* Stack items vertically */
        align-items: flex-start; /* Left align items on smaller screens */
        padding: 1rem;
    }
    
    nav a {
        margin-right: 0;
        padding: 0.8rem 1.5rem;
        width: 100%; /* Full width on smaller screens */
        text-align: center;
    }
}
